<template>
  <div>
    <!-- 头部 -->
    <header class="fl-ex al-it">
      <router-link to="/system" tag="img" src="../../../static/img/29.png" alt="图"></router-link>
      <p>会员管理</p>
    </header>
    <!-- 搜索 -->
    <div class="select">
      <a class="fl-ex al-it">
        <img src="../../../static/img/28.png" alt="搜索" />
        <p>|</p>
        <input type="text" placeholder="请输入会员姓名" />
      </a>
    </div>
  </div>
</template>

<script>
export default {};
</script>

<style scpoed>
header {
  width: 100%;
  height: 3.2rem;
  background-color: #df0001;
}
header img {
  width: 4%;
  height: 1.25rem;
  margin-left: 5%;
}
header p {
  font-size: 1.2rem;
  color: white;
  margin-left:32%;
}
/* 搜索 */
.select{
      width: 100%;
      height: 2.5rem;
       background-color: white;
       box-sizing: border-box;
    }
.select a{
      width:70%;
      height: 1.55rem;
      background-color: white;
      margin: 0.5rem 15% 0.5rem 15%;
      border-radius:1rem; 
      overflow: hidden;
      position: absolute;
      border: 0.05rem solid #cccccc;

    } 
.select a img{
      width: 1.05rem;
      height: 1rem;
      margin-left:5%;
    } 
    .select a p{
      margin-left:2%;
      color: #e3e3e3;
    } 
.select a input{
      width:80%;
      height: 2rem;
      background-color:white;
      font-size: 0.90rem;
      border: 0;
      margin-left:2%;
      color: #a4a4a4;
    }
.organization{
     width: 100%;
      height: 2.5rem;
      margin-top: 0.35rem;
       background-color: white;
       justify-content: flex-start;
 } 
 .organization p{
   font-size: 1rem;
   color: black;
   align-self: flex-start;
   margin-left:5%;
   margin-top: 4%;
 }
</style>


