<template>
  <div class="index">
       <!-- 头部 -->
    <Head></Head>
    <!-- 单位管理 -->
    <section class="nav-items" v-if="!this.show">
      <ul class="item-list fl-ex al-it ju-ar">
        <li
          class="item"
          v-for="(item,index) in itemList"
          :to="item.path"
          :class="{active:currentIndex==index}"
          @click="changeSelect(index)"
          :key="index"
          style="font-weight: bold; font-size:1rem;"
        >{{item.name}}</li>
      </ul>
      <router-view></router-view>
    </section>
  </div>
</template>

<script>
import Head from "./head-member";
export default {
    name: "NavItemHeader",
  data() {
    return {
      show: false,
      currentIndex: 0,
      itemList: [
        {
          name: "会员管理",
          path: "/meberOne"
        },
        {
          name: "会员档案",
          path: "/meberTwo"
        }
      ]
    };
  },
  methods: {
    changeSelect(index) {
      console.log(this.$router.push);
      //点击切换成选中的样式，把变量的值赋值为 index
      this.currentIndex = index;
      var _this = this;
      _this.$router.push({
        path: _this.itemList[index].path
      });
    }
  },
  components: {
    Head
  }
};
</script>

<style scoped>
.index {
  width: 100%;
  height: 100%;
  background-color: #e5e5e5;
}
/* 单位管理 */
.nav-items {
  background-color: white;
  width: 100%;
  height:2.5rem;
  margin-top: 2%;
}
/*左侧ul列表*/
.item-list {
  height:2.3rem;
}
/*列表中的每一项*/
.item-list > .item {
  display: inline-block;
  font-size: 1rem;
  color: #000000;
  text-align: center;
  padding-bottom:0.4rem; 
}
/*如果被选中的样式*/
.item-list > .active {
  border-bottom: 0.15rem solid #e10000;
  opacity: 1;
}
</style>


